<template>
    <div class="xui-vue markdown-section">
        <div class="main">
            <h1>modal</h1>
            <blockquote>
                <p>: ,?</p>
            </blockquote>
            <h5 id="">:</h5>
            <codes :codes="params"></codes>
            <ul>
                <li>,</li>
                <li><code>isShowClose</code></li>
                <li>,, <code>isShowClose</code><code>true</code>, </li>
                <li>,</li>
                <li>? : <code>me@xumengzi.top</code></li>
            </ul>
            <h3></h3>
            <p>1.,</p>
            <x-button type="default" @click="modal1"></x-button>
            <codes :codes="modal_1"></codes>
            <p>2.,,PM</p>
            <x-button type="default" @click="modal2"></x-button>
            <codes :codes="modal_2"></codes>
        </div>
    </div>
</template>

<script type="text/javascript">
import XButton from '@/plugins/form/button.vue';
import Codes from '@/assets/codes.vue';

export default {
    name: 'xui-loading',
    data(){
        return{
            params: 
`//
import Modal from './plugins/modal/modal.js';
Vue.use(Modal);

this.$modal({
    tips: string,
    text: string,
    isShowClose: boolean
    confirmBtn: {
        text: 'ok',
        fn(){
            console.log('ok');
        }
    },
    cancelBtn: {
        text: 'cancel',
        fn(){
            console.log('cancel');
        }
    },
})`,
            modal_1: 
`this.$modal({
    tips: 'Title',
    text: 'some comments',
})`,
            modal_2: 
`this.$modal({
    tips: 'Title',
    text: 'some comments',
    confirmBtn: {
        text: 'confirm',
        fn(){
            console.log('confirm');
        }
    },
    cancelBtn: {
        text: 'cancel',
        fn(){
            console.log('cancel');
        }
    },
})`,

        }
    },
    mounted(){
        this.$nextTick(function(){})
    },
    methods:{
        modal1(){
            this.$modal({
                tips: 'Title',
                text: 'some comments',
            });
        },
        modal2(){
            let that = this;
            that.$modal({
                tips: 'Title',
                text: 'some comments',
                isShowClose: false,
                confirmBtn: {
                    text: 'confirm',
                    fn(){
                        that.$message('confirm');
                    }
                },
                cancelBtn: {
                    text: 'cancel',
                    fn(){
                        that.$message('cancel');
                    }
                },
            })
        },
    },
    components:{
        XButton,
        Codes
    }
};
</script>

<style type="text/css">
    #test{
        height: 300px;
        position: relative;
        background: #ccc;
    }
</style>


