<template>
    <div class="xui-vue markdown-section">
        <div class="main">
            <h1>pagination</h1>
            <blockquote>
                <p>: /,</p>
            </blockquote>
            <h5 id="">:</h5>
            <codes :codes="params"></codes>
            <ul>
                <li><code>html</code>,<code>id</code>,<code>init</code>,</li>
                <li><code>index</code>,1</li>
                <li><code>total</code> <strong></strong></li>
                <li><code>isShowDot</code>...</li>
                <li><code>isShowNum</code>,<code>index/total</code></li>
                <li><code>isJumpPage</code>(),,</li>
                <li><code>click</code>,<code>ajax</code>,</li>
                <li><code>enter</code></li>
                <li>? : <code>me@xumengzi.top</code></li>
            </ul>
            <h3></h3>
            <p>1.,!</p>
            <x-pagination 
                :index="3"
                :total="5"
                @click="test1"
            ></x-pagination>
            <codes :codes="page_1"></codes>
            <p>2.,,</p>
            <x-pagination 
                :index="2"
                :total="10"
                isShowDot="true"
                isShowNum="true"
                isJumpPage="true"
                @click="test2"
                @enter="test2"
            ></x-pagination>
            <codes :codes="page_2"></codes>
        </div>
    </div>
</template>

<script type="text/javascript">
import XPagination from '@/plugins/pagination/pagination.js';
import Codes from '@/assets/codes.vue';

export default {
    name: 'xui-loading',
    data(){
        return{
            params: 
`//
import XPagination from '@/plugins/pagination/pagination.vue';

<x-pagination 
    :index="3"
    :total="10"
    isShowDot="true"
    isJumpPage="true"
    @click="test"
    @enter="test"
></x-pagination>

//
test(inx){
    console.log(inx);
}`,
            page_1: 
`<x-pagination 
    :index="3"
    :total="10"
    @click="test"
></x-pagination>
`,
            page_2: 
`<x-pagination 
    :index="2"
    :total="10"
    isShowDot="true"
    isJumpPage="true"
    @click="test2"
    @enter="test2"
></x-pagination>
`
        }
    },
    mounted(){
        this.$nextTick(function(){
        })
    },
    methods:{
        test1(inx){
            this.$message(inx, 300);
        },
        test2(inx){
            this.$message(inx, 300);
        }
    },
    components:{
        XPagination,
        Codes
    }
};
</script>

<style type="text/css">
    #test{
        height: 300px;
        position: relative;
        background: #ccc;
    }
</style>


