<template>
    <div class="xui-vue markdown-section">
        <div class="main" >
            <h1 id=""><code>vue</code></h1>
            <blockquote>
                <p>: <code>main.js</code>,</p>
                <p><code>directive.js</code>, </p>
            </blockquote>
            <h2>0.<code>directive.js</code>(,)</h2>
            <codes :codes="allDirectives"></codes>
            <hr>
            <blockquote>
                <p>: </p>
            </blockquote>
            <h2>1.<code>v-loadimg</code></h2>
            <blockquote>
                <p>: 404</p>
            </blockquote>
            <p>1.404, </p>
            <ul>
                <li>, </li> 
                <li>404, </li> 
                <li>? : <code>me@xumengzi.top</code></li>
            </ul>
            <codes :codes="loadimg"></codes>
            <h2>2.<code>v-clickoutside</code></h2>
            <blockquote>
                <p>: </p>
            </blockquote>
            <p>1.</p>
            <ul>
                <li>? : <code>me@xumengzi.top</code></li>
            </ul>
            <codes :codes="clickoutside"></codes>
            <h2>3.<code>v-copytoclipboard</code></h2>
            <blockquote>
                <p>: <code>focus</code><code>input</code>,</p>
            </blockquote>
            <p>1.</p>
            <ul>
                <li>? : <code>me@xumengzi.top</code></li>
            </ul>
            <codes :codes="copytoclipboard"></codes>
        </div>
    </div>
</template>

<script>
import Codes from '@/assets/codes.vue';

export default {
    name: 'show',
    data () {
        return {
            allDirectives: 
`//js
import directive from '@/assets/js/directive';
Vue.use(directive);
<div v-clickoutside=""></div>
<div v-for="(item, index) in 10" :key="index"><img v-loadimg="item.url"/></div>
`,
            clickoutside: 
`//you know what I mean?
Vue.directive('clickoutside', {
    bind (el, binding, vnode) {
        function handleClick (e) {
            if (el.contains(e.target)) {
                return false;
            };
            if (binding.expression) {
                binding.value(e);
            };
        };
        el.__vueClickOutSide__ = handleClick;
        document.addEventListener('click', handleClick);
    },
    unbind (el, binding) {
        document.removeEventListener('click', el.__vueClickOutSide__);
        delete el.__vueClickOutSide__;
    }
});
`,
            loadimg:
`//404
let loadingImg = '';
let notFoundImg = '';
Vue.directive('loadImg', {
    inserted(el, binding){
        //
        loadingImg && (el.src = loadingImg);
        //,url
        let img = new Image();
        img.src = binding.value;
        img.onload = () =>{
            el.src = binding.value;
        };
        img.onerror = () =>{
            if (notFoundImg) {
                el.src = notFoundImg;
            } else{
                let div = document.createElement('div');
                div.innerHTML = '';
                div.classList.add('noimg');
                el.parentNode.appendChild(div);
                el.remove();
            };
        };
    },
});
`,
            copytoclipboard: 
`//inputfocus
Vue.directive('copytoclipboard', {
    bind(el, binding){
        if (binding.arg == 'click') {
            el.onclick = copy;
        } else{
            el.onfocus = copy;
        };
        function copy(){
            if (document.execCommand('copy')) {
                if (el.select) {
                    el.select();
                    el.focus();
                    el.setSelectionRange(0, el.value.length);
                } else{
                    document.querySelector('.fake_input_value') && document.querySelector('.fake_input_value').remove();
                    let fakeInput = document.createElement('input');
                    fakeInput.style.position = 'absolute';
                    fakeInput.style.left = '-100%';
                    fakeInput.value = binding.value;
                    fakeInput.classList.add('fake_input_value');
                    fakeInput.setSelectionRange(0, el.value.length);
                    document.body.appendChild(fakeInput);
                    document.querySelector('.fake_input_value').select();
                    document.querySelector('.fake_input_value').focus();
                };
                document.execCommand('copy');
                document.querySelector('.fake_input_value') && document.querySelector('.fake_input_value').remove();
            } else{
                alert('!');
            };
        }
    },
    unbind(el, binding){
        document.querySelector('.fake_input_value') && document.querySelector('.fake_input_value').remove();
    }
})
`,
        }
    },
    components:{
        Codes,
    }
}
</script>


