<template>
    <div class="xui-vue markdown-section">
    	<div class="main">
    		<h1>slider</h1>
            <blockquote>
                <p>: </p>
            </blockquote>
            <h5 id="">:</h5>
            <codes :codes="params"></codes>
            <ul>
    			<li><code>options</code>,  <strong></strong></li>
    			<li><code>animation</code>, </li>
    			<li><code>duration</code></li>
    			<li><code>styles</code>, </li>
    			<li><code>change</code>,</li>
    			<li>? : <code>me@xumengzi.top</code></li>
    		</ul>
            <h3></h3>
            <p>1., </p>
            <div class="example">
            	<x-slider 
            		:options="imgList"
            		animation= '.5s linear'
            		duration="3000"
            		styles="hello"
            		@change="getCurrentPic"
            	/>
            </div>
            <codes :codes="slider_1"></codes>
    	</div>
    </div>
</template>

<script>
import XSlider from '@/plugins/slider/slider.vue';
import Codes from '@/assets/codes.vue';

export default {
	name: 'show',
	data () {
		return {
			imgList: [
		        {
		            link: 'http://xumengzi.top/',
		            imgUrl: 'http://img.infinitynewtab.com/wallpaper/1111.jpg',
		        },
		        {
		            link: 'http://xumengzi.top/gitbook/xui-vue/index.html#/',
		            imgUrl: 'http://img.infinitynewtab.com/wallpaper/1234.jpg',
		        },
		        {
		            link: 'https://jarveniv.gitbooks.io/xui/content/',
		            imgUrl: 'http://img.infinitynewtab.com/wallpaper/2345.jpg',
		        },
		    ],
			params: 
`//
import XSlider from '@/plugins/slider/slider.vue';

<x-slider 
    :options="imgList"
    animation= '.5s linear'
    styles="hello"
    duration="3000"
    @change="getCurrentPic"
/>

imgList: [
    {
        link: 'http://xumengzi.top/',
        imgUrl: 'http://img.infinitynewtab.com/wallpaper/1111.jpg',
    },
    {
        link: 'http://xumengzi.top/gitbook/xui-vue/index.html#/',
        imgUrl: 'http://img.infinitynewtab.com/wallpaper/1234.jpg',
    },
    {
        link: 'https://jarveniv.gitbooks.io/xui/content/',
        imgUrl: 'http://img.infinitynewtab.com/wallpaper/2345.jpg',
    },
],

//
getCurrentPic(data){
    console.log(data);
}
`,
			slider_1: 
`<x-slider 
    :options="imgList"
    animation= '.5s linear'
    styles="hello"
    duration="3000"
    @change="getCurrentPic"
/>
`,
		}
	},
	mounted(){
		this.$nextTick(function(){})
	},
	methods: {
		getCurrentPic(data){
			console.log(data);
		},
	},
	components:{
		XSlider,
		Codes
	}
}
</script>

<style type="text/css">
	.example{
	    width: 770px;
    	height: 300px;
	}
</style>


