<template>
	<div class="xui-vue markdown-section">
		<div class="main">
			<h1>digital</h1>
	        <blockquote>
	            <p>: ,.</p>
	        </blockquote>
	        <h5 id="">:</h5>
	        <codes :codes="params"></codes>
	        <ul>
				<li><code>html</code>,<code>id</code>,</li>
				<li><code>id</code><code>id</code> <strong></strong></li>
				<li><code>min</code>,1</li>
				<li><code>max</code>,1000</li>
				<li><code>step</code>,,1</li>
				<li><code>precision</code>,(),2</li>
				<li><code>isLimit</code><code>maxlength</code>,9</li>
				<li><code>isInput</code>,,<code>true</code></li>
				<li><code>fn</code>,</li>
				<li>,<code>xui_digital_small</code></li>
				<li>? : <code>me@xumengzi.top</code></li>
			</ul>
	        <h3></h3>
	        <p>1., </p>
	        <x-digital :defaultVal="1" 
		        :min="1" :max="10" 
	        ></x-digital>
	        <codes :codes="star_1"></codes>
	        <p>2.,.1,<code>precision</code></p>
	        <x-digital :defaultVal=".1" 
		        :min=".1" :max="1.0" 
		        :step=".1" :precision="1" 
		        :limit="3" size="true"
	        ></x-digital>
	        <codes :codes="star_2"></codes>
		</div>
	</div>
</template>

<script>
import XDigital from '@/plugins/digital/digital.vue';
import Codes from '@/assets/codes.vue';

export default {
	name: 'show',
	data () {
		return {
			params: 
`//
import XDigital from '@/plugins/digital/digital.vue';

<x-digital :defaultVal="1" :min="1" 
    :max="10" :step="2" 
    :precision="1" :limit="3" 
    size="true"
></x-digital>`,
			star_1: 
`<x-digital :defaultVal="1" 
    :min="1" :max="10" 
></x-digital>`,
			star_2: 
`<x-digital :defaultVal="1" 
    :min="1" :max="10" 
    :step="1" :precision="1" 
    :limit="3" size="true"
></x-digital>`
		}
	},
	mounted(){
		this.$nextTick(function(){})
	},
	methods: {
		open(){
			let that = this;
			that.$message(
				'hello, world', 
				1500, 
				function(){
					that.$message('callback');
				}
			);
		}
	},
	components:{
		XDigital,
		Codes
	}
}
</script>


