<template>
    <div class="xui-vue markdown-section">
        <div class="main">
            <h1>loading</h1>
            <blockquote>
                <p>: ,.</p>
            </blockquote>
            <h5 id="">:</h5>
            <codes :codes="params"></codes>
            <ul>
                <li>
                    <code>boolean</code><code>loading</code>,<code>false</code>,<code>loading</code><strong></strong>
                </li>
                <li>
                    <code>string</code><code>loading</code>, <code>loading</code>, 
                </li>
                <li>
                    ? : <code>me@xumengzi.top</code>
                </li>
            </ul>
            <h3></h3>
            <p>1.<code>loading</code></p>
            <x-button type="default" @click="loading1"></x-button>
            <codes :codes="loading_1"></codes>
            <p>2.<code>loading</code></p>
            <blockquote>
                <p>tips: <code>position:relative;</code></p>
            </blockquote>
            <x-button type="default" @click="loading2"></x-button>
            <div id="test"></div>
            <codes :codes="loading_2"></codes>
        </div>
    </div>
</template>

<script type="text/javascript">
import XButton from '@/plugins/form/button.vue';
import Codes from '@/assets/codes.vue';

export default {
    name: 'xui-loading',
    data(){
        return{
            params: 'this.$loading(boolean, string);',
            loading_1: 'this.$loading(true);',
            loading_2: 'this.$loading(true, "#test");',
        }
    },
    mounted(){
        this.$nextTick(function(){
        })
    },
    methods:{
        loading1(){
            this.$loading(true);
            setTimeout(()=>{
                this.$loading(false);
            },2000);
        },
        loading2(){
            this.$loading(true, '#test');
            setTimeout(()=>{
                this.$loading(false);
            },2000);
        }
    },
    components:{
        XButton,
        Codes
    }
};
</script>

<style type="text/css">
    #test{
        height: 300px;
        position: relative;
        background: #ccc;
    }
</style>


