<template>
	<div class="xui-vue markdown-section">
		<div class="main">
			<h1 id=""></h1>
			<h2 id="-xui">: <code>{{$store.getters.projectName}}</code></h2>
			<ul>
				<li>
					<p>: <code>vueJs2.0</code>,,,.</p>
				</li>
				<li>
					<p>: <code>vuejs</code></p>
				</li>
				<li>
					<p>: IE9</p>
				</li>
				<li>
					<p>: (<code>vue</code>, <code>react</code>, <code>angular</code>, <code>etc</code>)<code>plugins</code></p>
				</li>
			</ul>
			<blockquote>
				<p>: js,css, .</p>
			</blockquote>
			<blockquote>
				<p>: js,jses6,babel
				</p>
			</blockquote>
			<blockquote>
				<p>: assets</p>
			</blockquote>
			<h5>,,.</h5>
			<h5></h5>
			<h6>: <em>me@xumengzi.top</em></h6>
		</div>
	</div>
</template>

<script>
import { mapGetters, mapMutations, mapActions, mapState } from 'vuex';

import From from '../docs/from.vue';
import Plugins from '../docs/plugins/plugins.vue';
import Codes from '../assets/codes.vue';
import master from '@/components/master.vue'

export default {
	name: 'index',
	data () {
		return {
			msg: 'Welcome to my xui-vue App',
			introduce: 'How many codes must a man edit before you call him a master?'
		}
	},
	created(){
	},
	mounted(){
		this.$nextTick(function(){
			this.$store.commit('updateList', {
				value: 4,
				label: 'jquery',
			});
			this.updateList({
				value: 4,
				label: 'jquery',
			});
		})
	},
	computed: {
		...mapGetters([
			'filterListEs5'
		]),
		...mapState({
			version: state => state.version
		}),
	},
	methods: {
		add(){
			this.a++;
		},
		...mapActions([
			'introList',
		]),
		...mapMutations([
			'updateList'
		]),
	},
	components:{
		From,
		master,
		Plugins,
	}
}
</script>



