<template>
    <div class="xui-vue markdown-section">
        <div class="main">
            <h1>select</h1>
            <blockquote>
                <p>: </p>
            </blockquote>
            <h5>:</h5>
            <codes :codes="params"></codes>
            <ul>
                <li><code>list</code>,<strong></strong></li>
                <li><code>selectVal</code>,<code>key</code><code>value</code>,</li>
                <li><code>defaultVal</code>,<code>value</code>,0</li>
                <li><code>isSearch</code>,<code>false</code></li>
                <li><code>placeholder</code>,<code>search</code></li>
                <li>? : <code>me@xumengzi.top</code></li>
            </ul>
            <h3></h3>
            <p>1.</p>
            <x-dropdown
                :list="list"
                :selectVal="selectedVal"
                @handleChange="showVal"
                >
            </x-dropdown>
            <codes :codes="select_1"></codes>
            <p>2., <code>defaultVal</code>.<code>isSearch</code><code>true</code></p>
            <blockquote>
                <p>: ,<code>key</code><code>value</code></p>
            </blockquote>
            <x-dropdown
                :list="list"
                :selectVal="selectedVal"
                @handleChange="showVal"
                :isSearch="true"
                placeholder="type to search"
                :defaultVal="2"
                >
            </x-dropdown>
            <codes :codes="select_2"></codes>
        </div>
    </div>
</template>

<script>
import XDropdown from '@/plugins/dropdown/dropdown.vue';

import Codes from '@/assets/codes.vue';

export default {
    name: 'show',
    data () {
        return {
            list: [
                {
                    value: 1,
                    key: 'vueJs',
                },
                {
                    value: 2,
                    key: 'reactJs',
                },
                {
                    value: 3,
                    key: 'angularJs',
                },
                {
                    value: 4,
                    key: 'javascript',
                },
            ],
            selectedVal: {
                key: '',
                value: '',
            },
            params: 
`//
import XDropdown from '@/plugins/dropdown/dropdown.vue';

<x-dropdown
    :list="list"
    :selectVal="selectedVal"
    :isSearch="true"
    placeholder="type to search"
    @handleChange="showVal"
    :defaultVal="2"
    >
</x-dropdown>
//data
list: [
    {
        value: 1,
        key: 'vueJs',
    },
    {
        value: 2,
        key: 'reactJs',
    },
    {
        value: 3,
        key: 'angularJs',
    }
],
selectedVal: {
    key: '',
    value: '',
},
//methods
showVal(){
    console.log(this.selectedVal);
}
`,
            select_1: 
`<x-dropdown
    :list="list"
    :selectVal="selectedVal"
    @handleChange="showVal"
>
</x-dropdown>
`,
            select_2: 
`//
<x-dropdown
    :list="list"
    :selectVal="selectedVal"
    @handleChange="showVal"
    :isSearch="true"
    placeholder="type to search"
    :defaultVal="2"
>
</x-dropdown>
`
        }
    },
    mounted(){
        this.$nextTick(function(){})
    },
    methods: {
        showVal(){
            // console.log(this.selectedVal);
        },
    },
    components:{
        XDropdown,
        Codes,
    }
}
</script>


