<template>
    <div class="xui-vue markdown-section">
        <div class="main">
            <h1>notification</h1>
            <blockquote>
                <p>: .</p>
            </blockquote>
            <h5 id="">:</h5>
            <codes :codes="params"></codes>
            <ul>
                <li>
                    <code>title</code>
                </li>
                <li>
                    <code>content</code>
                </li>
                <li>
                    <code>duration</code>, 0, 3s
                </li>
                <li>
                    <code>btn</code>, 
                </li>
                <li>
                    <code>fn</code>
                </li>
                <li>
                    ? : <code>me@xumengzi.top</code>
                </li>
            </ul>
            <h3></h3>
            <p>1., </p>
            <x-button type="default" @click="notification1"></x-button>
            <codes :codes="notification_1"></codes>
            <p>2., </p>
            <x-button type="default" @click="notification2"></x-button>
            <codes :codes="notification_2"></codes>
        </div>
    </div>
</template>

<script type="text/javascript">
import XButton from '@/plugins/form/button.vue';
import Codes from '@/assets/codes.vue';

export default {
    name: 'xui-loading',
    data(){
        return{
            params: 
`//
import XNotification from './plugins/notification/notification.js';
Vue.use(XNotification);

this.$notification({
    title: string,
    content: string,
    duration: number,
    btn: string,
    fn: callback,
});
`,
            notification_1: 
`this.$notification({
    title: 'notification title',
    content: 'This is the content of the notification.',
});
`,
            notification_2: 
`this.$notification({
    title: 'notification title',
    content: 'This is the content of the notification.',
    duration: 5000,
    fn(){
        console.log('notification closed');
    },
});
`,
        }
    },
    mounted(){
        this.$nextTick(function(){
        })
    },
    methods:{
        notification1(){
            let that = this;
            this.$notification({
                title: 'notification title',
                content: 'This is the content of the notification.',
            });
        },
        notification2(){
            let that = this;
            this.$notification({
                title: 'notification title',
                content: 'This is the content of the notification.',
                duration: 5000,
                btn: 'confirm',
                fn(){
                    that.$message('notification closed');
                },
            });
        },
    },
    components:{
        XButton,
        Codes
    }
};
</script>

<style type="text/css">
    #test{
        height: 300px;
        position: relative;
        background: #ccc;
    }
</style>


