<template>
    <div class="xui-vue markdown-section">
        <div class="main">
            <h1>tab</h1>
            <blockquote>
                <p>: </p>
            </blockquote>
            <h5 id="">:</h5>
            <codes :codes="params"></codes>
            <ul>
                <li><code>tabList</code><strong></strong> <code>Array</code></li>
                <li><code>direction</code> <code>tab</code>,<code>x</code>, <code>String</code></li>
                <li><code>activeIndex</code><code>tab</code>, 0 <code>Number</code></li>
                <li><code>change</code>,<code>this</code> <code>Function</code></li>
                <li>? : <code>me@xumengzi.top</code></li>
            </ul>
            <h3></h3>
            <p>1.<code>tabList</code>,</p>
            <blockquote>
                <p>: ,</p>
            </blockquote>
            <x-tab :tabList="tabs"></x-tab>
            <codes :codes="tab_1"></codes>
            <p>2., tab</p>
            <x-tab :tabList="tabs" :direction="vertical" :activeIndex="-3" @change="callback($event)"></x-tab>
            <codes :codes="tab_2"></codes>
        </div>
    </div>
</template>

<script>
import XTab from '@/plugins/tab/tab.vue';
import Codes from '@/assets/codes.vue';

export default {
    name: 'show',
    data () {
        return {
            tabs: [
                {
                    title: '<em>1</em>',
                    content: '1',
                },
                {
                    title: '2',
                    content: '<p style="color:red;">p,</p>',
                },
                {
                    title: '3',
                    content: '3',
                }
            ],
            vertical: 'y',
            params: 
`//
import XTab from '@/plugins/tab/tab.vue';

<x-tab :tabList="tabs" :direction="vertical" :activeIndex="1" @change="callback($event)"></x-tab>

tabs: [
    {
        title: '<em>1</em>',
        content: '1',
    },
    {
        title: '2',
        content: '<p style="color:red;">p,</p>',
    },
    {
        title: '3',
        content: '3',
    }
],
`,
            tab_1: '<x-tab :tabList="tabs"></x-tab>',
            tab_2: '<x-tab :tabList="tabs" :direction="vertical" :activeIndex="1" @change="callback($event)"></x-tab>',
        }
    },
    mounted(){
        this.$nextTick(function(){})
    },
    methods: {
        callback(tar){
            console.log(tar);
        }
    },
    components:{
        XTab,
        Codes
    }
}
</script>

<style type="text/css">
.xui_tab {
    width: 800px;
    height: 300px;
}
</style>


